* {
  list-style-type: none;
}
.form {
  padding: 1rem;
  background-color: #345;
  height: 500px;
  width: 25%;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  border-radius: 20px;
  box-shadow: 0 0 30px 5px #333;
}
button {
  color: aliceblue;
  padding: 1rem;
  border-radius: 50%;
  border: 1px solid #24a;
  background-color: #24a;
  cursor: pointer;
  margin: auto 1rem;
  font-size: 18px;
  outline: none;
}
button:hover {
  background-color: #2a2;
}
.inputval {
  border: none;
  padding: 1rem;
  outline: none;
  background-color: #345;
  border-bottom: 2px solid #fff;
  color: #fff;
  text-transform: capitalize;
  transition: 0.5s;
  font-size: 18px;
}
.inputval:focus {
  transform: scaleX(1.1);
  color: #fff;
}
.todo_list {
  padding-left: 0px;
  padding: 1rem;
}
.todo_style {
  color: #002;
  text-transform: capitalize;
  font-size: 16px;
  padding: 0.5rem;
  background-color: #fdd;
  margin-bottom: 10px;
  cursor: pointer;
  display: flex;
  justify-content: left;
  text-align: center !important;
  flex-direction: row;
}
h1 {
  margin: 0.5rem auto !important;
  text-transform: capitalize;
  color: #fde;
  background-color: #24a;
  padding: 0.5rem;
}
.todo_style .fa-times {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  background-color: #233;
  color: #f01;
  align-items: center;
  margin: auto 5px;
}
@media (max-width: 576px) {
  .form {
    width: 80%;
    margin: auto;
    padding: 0.5rem;
  }
  .inputval {
    padding: 0.3rem;
    font-size: 15px;
  }
  button {
    padding: 0.5rem;
    margin: auto 0.3rem;
  }
}
